:root {
  color-scheme: dark;
  scrollbar-color: white black;
}

body {
  overflow: hidden;
  -webkit-overflow-scrolling: touch;

  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #2234ae;
  background-image: linear-gradient(315deg, #2234ae 0%, #191714 20%);
  background-attachment: fixed;
  height: 100vh;
}


.grid-grid {
  display: flex;
  flex-direction: column;
  align-items: center;

  color: ghostwhite;
  margin-top: 5%;
  font-weight: bold;
  height: 75vh;
}

.grid-row {
  display: flex;
  justify-content: center;
}

.grid-hint {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.row-hint-number {
  padding: 1px;
  font-size: 10pt;
}

.tile {
  border: solid ghostwhite;
  transition: 0.5s;
}

.hint-solved {
  color: gray;
}

.tile-unselected:not(.tile-solved) {
  cursor: pointer;
}

.tile-unselected:hover {
  background-color: ghostwhite;
}

.tile-selected {
  background-color: ghostwhite;
}

.tile-selected.tile-solved {
  background: ghostwhite;
}

.tile-unselected.tile-solved {
  opacity: 0.3;
  background: lightgray
    repeating-linear-gradient(
      45deg,
      #000000,
      #000000 2.5px,
      ghostwhite 2.5px,
      ghostwhite 22.5px
    );
}

.error-field {
  text-align: center;
  color: red;
  font-weight: bold;
  margin-top: 1.25em;
}

.new-game-container {
  width: 100%;
  display: flex;
  justify-content: center;
}

.new-game-button {
  background: none;
  margin-top: 1.25em;
  transition: 0.5s;
  font-weight: bold;
  border: none;
  font-size: 16pt;
}

.new-game-button:hover {
  text-decoration: underline;
}

.success-field {
  margin-top: 20px;
  text-align: center;
  color: greenyellow;
  font-weight: bold;
}

